export const CheckboxHidden = Styled.div(({ checked }) => {
  return {
    height: BASE_SIZE * 2 - 4,
    width: BASE_SIZE * 2 - 4,
    paddingTop: 2,
    paddingLeft: 2,
    backgroundColor: checked ? "var(--accent-color)" : "white",
    borderRadius: BASE_RADIUS,
    boxShadow: checked
      ? "inset rgba(0, 0, 0, 0.07) 0 0 0 1px"
      : "inset rgba(0, 0, 0, 0.33) 0 0 0 1.5px",
    cursor: "pointer",
  };
});

export const Checkbox = function Checkbox({ label, checked, onChange }) {
  return React.createElement(
    Inline,
    {
      onClick: () => {
        if (onChange) {
          onChange(!checked);
        }
      },
      style: {
        alignItems: "center",
        cursor: "pointer",
      },
    },
    [
      React.createElement(
        CheckboxHidden,
        {
          key: 0,
          checked,
        },
        React.createElement(
          "svg",
          {
            xmlns: "http://www.w3.org/2000/svg",
            width: "24",
            height: "24",
            fill: "none",
            stroke: "currentColor",
            strokeLinecap: "round",
            strokeLinejoin: "round",
            strokeWidth: "2",
            className: "feather feather-check",
            viewBox: "0 0 24 24",
            color: checked ? "white" : "#ddd",
          },
          React.createElement("path", {
            d: "M20 6L9 17 4 12",
          })
        )
      ),
      React.createElement(Space, { key: 1, w: 0.5 }),
      React.createElement(
        Text,
        { key: 2, style: { userSelect: "none" } },
        label
      ),
    ]
  );
};

